<template>
  <div class="login-page">
    <div class="login-header">
      <div class="header-wrapper">
        <div class="header-name"><span>后台</span><span class="sep">|</span><span>管理系统</span></div>
        <!--<div class="header-btn">
          <Button type='primary' size='large' @click='$router.push("/register")'>企业注册</Button>
        </div>-->
      </div>
    </div>
    <div class="main-container">
      <div class="login-main clear-fix">

        <div class="layout">
          <div class='logintabs clear-fix'>
            <!--<div @click='loginType="wxlogin"' :class='{activetab:loginType==="wxlogin"}'>微信扫码登录</div>-->
            <div @click='loginType="snslogin"'  style="text-align: center;width:100%">手机帐号登录</div>
          </div>
          <div class="tabcontent ">
            <div class="snstab" v-if='loginType==="snslogin"'>
              <Form ref="formValidate" :model="login" :rules="rules">
                <FormItem prop="mobile" style='margin-top:30px;'>
                  <Input v-model.trim="login.mobile" placeholder="请输入手机号码！" clearable size='large'></Input>
                </FormItem>
                <FormItem prop="code" style='margin-top:30px;'>
                  <Input v-model.trim="login.code" placeholder="请输入短信验证码！" clearable size='large'>
                  <Button slot="append" type='ghost' style='width:120px' v-if='!showCount' @click='handleGetCode' :loading='codeLoading'>{{codeLoading?'正在获取':'点击获取验证码'}}</Button>
                  <span slot="append" v-else class="count">{{countnum}}秒后重新获取</span>
                  </Input>
                </FormItem>
                <Button type="primary" :loading="loading" @click="handleSubmit('formValidate')" size="large" class='submit-btn'>
                  <span v-if="!loading">登录</span>
                  <span v-else>正在登录..</span>
                </Button>
              </Form>
            </div>
            <div class="wxtab" v-if='loginType==="wxlogin"'>
              <wx-login></wx-login>
            </div>
          </div>
        </div>
       <!-- <div class="login-text">
          <p style='font-size:30px;margin-top:90px;height:80px;line-height:80px;letter-spacing:2px;font-weight:bold;color:#46566b'>凡人之家小程序</p>
          <p style='font-size:16px;height:30px;line-height:30px;letter-spacing:1px;'>超多小程序模板自由选择</p>
          <p style='font-size:16px;height:30px;line-height:30px;letter-spacing:1px;'>智能化管理 , 集高效美观于一体</p>
        </div>-->
      </div>
    </div>
    <div class="login-footer">
      <p style="text-align:left">&copy <span class="copyfont">Copyright</span> 2015-2018 后台管理系统 All Rights Reserved</p>
      <!-- <p style="text-align:right">杭州蓝米美业科技有限公司 <span>浙ICP备15029334号-2</span> &nbsp &nbsp<img class="copyright-pic" src="http://pic.meisaas.com/index/ga.png" alt="" style="width:20px;height:20px;"><span>京公网安备11000002000001号</span></p> -->
    </div>
  </div>
</template>
<script>
import { storeToken } from '../../utils.js'
import wxLogin from './wxlogin.vue'
export default {
  data() {
    let storeMobile = localStorage.getItem('mobile');
    let storeCode = localStorage.getItem('code')
    return {
      loginType: 'snslogin',
      login: {
        mobile: storeMobile || '',
        code:'',
      },
      showCount: false,
      countnum: 60,
      codeLoading: false,
      loading: false,
      rules: {
        mobile: [{ required: true, message: '请输入手机号码！', trigger: 'blur' }],
        code: [{ required: true, message: '请输入短信验证码！'}]
      }
    }
  },
  methods: {
    handleSubmit(name) {
      let form = this.$refs[name]
      if (!form) {
        return
      }
      form.validate(valid => {
        if (valid) {
          this.handleLogin()
        } else {
          this.$Message.error('请填写正确的登录信息!')
        }
      })
    },
    handleLogin() {
      this.loading = true
      return this.$http.post(this.$api.administrator_smslogin, this.login).then(res => {
        let data = res.data
        if (data.result != 1) {
          this.$Message.error(data.msg)
        } else {
          storeToken({ merid: res.data.data[0].merid, token: res.data.data[0].token })
          localStorage.setItem('mobile', this.login.mobile)
          localStorage.setItem('code', this.login.code)
          this.$Message.success('登录成功')
          this.$router.push('/')
        }

      }).catch(erro => {
        this.$Message.error('登录失败!')
      }).then(res => {
        this.loading = false
      })
    },
    handleGetCode() {
      if (!this.login.mobile) {
        this.$Message.error('请输入手机号码！')
        return
      }
      this.codeLoading = true
      return this.$http.post(this.$api.sms_checkcode, { mobile: this.login.mobile }).then(res => {

        if (res.data.result == '1') {
          this.getCode()
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch(erro => {
        this.$Message.error('获取验证码失败!', erro)
      }).then(res => {
        this.codeLoading = false
      })
    },
    getCode() {
      this.showCount = true;
      this.countnum = 60;
      this.timer();
    },
    timer() {
      if (this.countnum > 0) {
        this.countnum--;
        setTimeout(this.timer, 1000)
      } else {
        this.showCount = false
      }
    },
    goWxlogin() {
      this.$router.push('/wxlogin')
    },
  },
  components: {
    'wx-login': wxLogin,
  }
}

</script>
<style scoped>
.login-page {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

.login-header {
  height: 100px;
  font-size: 30px;
  width: 100%;
}

.header-wrapper {
  width: 90%;
  min-width: 900px;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

.header-name {
  height: 100%;
}

.header-name a {
  color: inherit;
}

.header-name>span {
  display: inline-block;
  line-height: 100px;
  height: 100%;
  vertical-align: middle;
  color: #333;
  margin: auto 0;
}

.header-name>.sep {
  margin: 0 15px 0 14px;
  width: 1px;
  font-weight: normal;
  color: #ccc;
}

.header-btn {
  position: absolute;
  right: 10px;
  top: 0;
  width: 120px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.logintabs {
  font-size: 16px;
  height: 60px;
  line-height: 60px;
  border-bottom: 1px solid silver;
}

.logintabs>div {
  float: left;
  width: 50%;
  height: 100%;
  line-height: inherit;
  text-align: center;
  cursor: pointer;
  margin-top: 1px;

}

.activetab {
  border-bottom: 2px solid #3bc0c3;
  color: #3bc0c3;
}

.login-main {
  width: 90%;
  min-width: 900px;
  height: 480px;
  margin: 50px auto;
}

.login-text {
  float: right;
  width: 400px;
  color:#6a737b;
  /*background-color: pink;*/
  margin-right: 100px;
  height: 450px;
}

.wx-login {
  border-top: 1px solid silver;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: 75px;
}

.submit-btn {
  width: 100%;
  height: 45px;
  margin: 20px 0;
}

.count {
  display: inline-block;
  width: 120px;
  color: #68cac9;
}

.main-container {

  background: url(http://pic.meisaas.com/admin/bg/bj.png) no-repeat center;
  background-size: cover;
  width: 100%;
  height: calc(100% - 200px);
}

.layout {
  float: right;
  width: 356px;
  background-color: #fdfdfd;
  border-radius: 10px;
  opacity: 0.9;

}

.title {
  font-size: 30px;
  height: 80px;
  line-height: 80px;
  text-align: center;
}

.tabcontent {
  margin: 0 auto;
  /*水平居中*/
  position: relative;
  vertical-align: middle;
  height: 380px;

  padding: 20px 40px;
  /*偏移*/
}

.login-footer {
  height: 100px;
  box-sizing: border-box;
  padding: 0 80px;
  overflow: hidden;
  color:#999;
}

.login-footer p {
  font-size: 14px;
  text-align: center;
  line-height: 100px;
  float: left;
  width: 50%;
}

.login-footer a {
  color: #999
}

.wxtab,
.snstab {
  /* height: 390px;
  background-color: pink;*/
}

.copy-font {
  color: #999;
}

</style>



