<template>
  <div>
    <div class='wx-pic' id='login_container' ref='login_container'></div>
    <div class="tips">
      <span>请使用微信扫描二维码登录 </span><span class="reflesh" @click='handleReflesh'><Icon type="refresh"></Icon>刷新</span>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {}
  },
  mounted() {
    var obj = new WxLogin({
      self_redirect: false,
      id: "login_container",
      appid: "wx5c1590027955d73b",
      scope: "snsapi_base",
      redirect_uri: "http://console.meipins.com/#/login",
    });
  },
  methods: {
    handleReflesh() {
      this.$nextTick(() => {
        let obj = new WxLogin({
          self_redirect: false,
          id: "login_container",
          appid: "wx5c1590027955d73b",
          scope: "snsapi_base",
          redirect_uri: "http://console.meipins.com/#/login",
        });
      })

    },
  }

}

</script>
<style scoped>
/*.layout {
  position: relative;
  width: 500px;
  padding: 20px 10px;
  margin: 0 auto;
  top: 50%;
  margin-top: -150px;
  background-color: #fff;
  border-radius: 10px;
  opacity: 0.95;
  min-height: 500px;
  filter: alpha(opacity=95)
}
*/

.wx-pic {
  width: 300px;
  height: 300px;
  margin: 0 auto;
}

.tips {
  width: 300px;
  margin: 10px auto;
  text-align: center;
}

.reflesh {
  color: #3bc0c3;
  cursor: pointer;
}

</style>



